@charset "utf-8";
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
li{
    list-style: none;
}
a{
    text-decoration: none;
    color: inherit;
}
body {
    color: #333;
    background-color:rgb(60, 82, 100);
}

.check {
    max-width: 640px;
    width: calc(100% - 30px);
    margin: 0 auto;
    
}
.check form {
    position: relative;
 
}
.check form input[type="text"] {
    width: 100%;
    height: 50px;
    border: 0;
    padding: 10px;
    border-radius: 10px;
}
.check form button {
   position: absolute;
   top: 50%;
   right: 5px;
   width: 40px;
   height:40px;
   background-color: rgba(255, 255, 255, 0.822);
   border: 0;
   transform: translate(0,-50%);
  
}
.check form button img {
    width: 20px;
    height: 20px;
    object-fit: cover;
}
.check_list {
    margin: 20px 0 ;
    border-top: 2px solid #8d8da3;
}
.check_list > ul {
    margin: 20px 0 ;
    
}
.check_list > ul >li {
   padding: 20px;
   background-color: rgba(255, 255, 255, 0.219);
   margin-bottom: 20px;
   border-radius: 10px;
   color: white;
   position: relative;
}

.check_list ul li button {
    width: 30px;
    height: 30px;
    background-color: inherit;
    border: 0;
    border-radius: 15px;
    background-color: rgba(12, 12, 12, 0.418);
    position: absolute;
    top: 20px;
    right: 20px;
}
.check_list ul li button img {
    width: 100%;
    height:100%;
    object-fit: cover;
    padding: 10px;
}
.check_list > ul >li .date{
    display: flex;
    color:#ccc;
}

.header {
    color: white;
}
.header h1 {
    text-align: center;
    margin-top: 40px;
}
.header span {
    text-align: center;
    display: block;
    margin-bottom: 40px;
}